<template>
  <div>
    <div class="fasilitas-overview">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <base-header class="pt-4 pb-8" type="primary"></base-header>
      <div class="container-fluid mt--8">
        <div class="row">
          <div class="col">
            <div class="card border-0">
              <div
                id="map-custom"
                class="map-canvas"
                style="height: calc(100vh - 512px); min-height: 350px"
              ></div>
            </div>
          </div>
        </div>
        <div class="row">
          <template v-for="item in tableData">
            <div :key="item.id" class="col-lg-4 col-md-6">
              <div class="card">
                <!-- Card image -->
                <img
                  class="card-img-top"
                  :src="item.cover[0].path"
                  alt="Image placeholder"
                />
                <!-- Card body -->
                <div class="card-body">
                  <h5 class="h2 card-title text-nowrap overflow-hidden mb-0">
                    {{ item.name }}
                  </h5>
                  <h6
                    class="font-weight-normal text-muted text-nowrap overflow-hidden m-0"
                  >
                    {{ getUnitName(item.unit) }}
                  </h6>
                  <h6
                    class="font-weight-normal text-muted text-nowrap overflow-hidden m-0"
                  >
                    {{ unixTS2DBYHMS(item.timestamp) }}
                  </h6>
                  <p class="my-2 text-truncate">{{ item.overview }}</p>
                  <a
                    :href="'#/fasilitas/id/' + item.id"
                    class="btn btn-link p-0"
                    >View article</a
                  >
                </div>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Cabang from "../cabang";
import Detail from "./detail";
import { API_KEY } from "../../Maps/API_KEY";
import GoogleMapsLoader from "google-maps";
import Axios from "axios";

GoogleMapsLoader.KEY = API_KEY;

const openUnitQuery = (id) => {
  this.$router.push("unit/id/" + id);
};

export default {
  data() {
    return {
      dummy: {
        isDummy: false,
        jenis: [
          {
            id: "alpha",
            name: "Alpha",
          },
          {
            id: "bravo",
            name: "Bravo",
          },
          {
            id: "charlie",
            name: "Charlie",
          },
          {
            id: "delta",
            name: "Delta",
          },
        ],
      },
      cabang: Cabang,
      detail: Detail,
      mapData: null,
      tableData: null,
    };
  },
  computed: {},
  methods: {
    getUnitName(id) {
      for (let index = 0; index < this.cabang.length; index++) {
        if (this.cabang[index].value === id) {
          return this.cabang[index].label;
        }
      }
    },
    getType() {
      Axios.get(
        this.$store.getters.getAPI.url + this.$store.getters.getAPI.fs.getType
      )
        .then((response) => {
          if (this.$store.getters.isDebugging) {
            console.log("DEBUG: Success to type list. Data: ", response.data);
          }
          let array = [];
          for (let i = 0; i < response.data.length; i++) {
            array.push({
              value: response.data[i].ID,
              label: response.data[i].NAME,
            });
          }
          array.sort((a, b) => {
            let aVal = a.label;
            let bVal = b.label;
            return bVal - aVal;
          });
          this.jenis = array;
          this.$store.commit("setJenisFasilitas", array);
        })
        .catch((error) => {
          if (this.$store.getters.isDebugging) {
            console.log("ERROR: Failed to get rating list. ", error);
          }
        });
    },
    getTableFasilitas() {
      if (this.dummy.isDummy) {
        let array = [];
        const count = 12;
        for (let index = 0; index < count; index++) {
          array.push({
            id: this.detail.id + index,
            cover: this.detail.cover,
            name: this.detail.name,
            unit: this.detail.unit,
            timestamp: this.detail.timestamp,
            overview: this.detail.overview,
            content: this.detail.content,
          });
        }
        this.tableData = array;
      } else {
        Axios.get(
          this.$store.getters.getAPI.url +
            this.$store.getters.getAPI.fs.getFSUnit +
            9999
        )
          .then((response) => {
            if (this.$store.getters.isDebugging) {
              console.log(
                "DEBUG: Receiving data for unit ID: 9999",
                response.data
              );
            }
            let array = [];
            for (let index = 0; index < response.data.length; index++) {
              array.push({
                id: response.data[index].FC_ID,
                cover: JSON.parse(response.data[index].COVER),
                name: response.data[index].NAME,
                unit: response.data[index].UNITKERJA_ID,
                timestamp: response.data[index].TIMESTAMP,
                overview: response.data[index].OVERVIEW,
              });
            }
            this.tableData = array;
          })
          .catch((error) => {
            console.log("ERROR: Failed to load data.", error);
          });
      }
    },
    initMap(google) {
      Axios.get("http://45.127.133.75:3000/api/iems/map")
        .then((response) => {
          let array = [];
          response.data.forEach((item) => {
            array.push({
              id: item.UNITKERJA_ID,
              name: item.UK_NAME,
              lat: item.UK_LAT,
              long: item.UK_LONG,
            });
          });
          this.mapData = array;
        })
        .then(() => {
          GoogleMapsLoader.load((google) => {
            let map,
              lat = -2,
              lng = 105;
            map = document.getElementById("map-custom");
            let myLatlng = new google.maps.LatLng(lat, lng);
            let mapOptions = {
              zoom: 5,
              scrollwheel: false,
              center: myLatlng,
              mapTypeId: google.maps.MapTypeId.ROADMAP,
            };
            map = new google.maps.Map(map, mapOptions);
            this.mapData.forEach((item) => {
              let marker = new google.maps.Marker({
                icon: "http://maps.google.com/mapfiles/ms/icons/orange-dot.png",
                position: new google.maps.LatLng(item.lat, item.long),
                map: map,
                animation: google.maps.Animation.DROP,
                title: item.name,
              });
              google.maps.event.addListener(marker, "click", () => {
                window.location.href = "#/fasilitas/unit/id/" + item.id;
              });
            });
          });
        });
    },
    unixTS2DBYHMS(UNIX_timestamp) {
      var a = new Date(UNIX_timestamp * 1000);
      var months = [
        "Januari",
        "Februari",
        "Maret",
        "April",
        "Mei",
        "Juni",
        "Juli",
        "Agustus",
        "September",
        "Oktober",
        "November",
        "Desember",
      ];
      var year = a.getFullYear();
      var month = months[a.getMonth()];
      var nmonth = a.getMonth() + 1;
      var date = a.getDate();
      var hour = a.getHours();
      var min = a.getMinutes();
      var sec = a.getSeconds();
      var time =
        date + " " + month + " " + year + " " + hour + ":" + min + ":" + sec;
      var xtime = date + "/" + nmonth + "/" + year;
      return time;
    },
  },
  created() {
    this.getType();
    this.initMap();
    this.getTableFasilitas();
  },
  mounted() {},
};
</script>

<style lang="scss">
.fasilitas-overview {
  .card-img-top {
    height: 240px;
    object-fit: cover;
  }
}
</style>
