import axios from "axios";

function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const nameA = a.label.toUpperCase();
  const nameB = b.label.toUpperCase();

  let comparison = 0;
  if (nameA > nameB) {
    comparison = 1;
  } else if (nameA < nameB) {
    comparison = -1;
  }
  return comparison;
}

let array = [];
axios.get("http://45.127.133.75:3000/api/uk/9999").then(response => {
  for (let i = 0; i < response.data.length; i++) {
    array.push({
      label: response.data[i].UK_NAME,
      value: response.data[i].UNITKERJA_ID
    });
  }
  array.sort(compare);
  array.unshift({ label: "Semua Unit Kerja", value: 9999 });
});

export default array;
